.display-options {
  height: 100px;
  position: absolute;
  border: 1px solid grey;
  width: 250px;
  background-color: white;
  border-radius: 10px;

  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: white;
}

.display-options > label {
  width: 80%;
  display: flex;
  justify-content: space-between;
}
.display-options > label > select {
  padding: 5px;
  border-radius: 5px;
}
