.ticket-card {
  padding: 10px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.ticket-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ticket-meta {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 10px;
}

.ticketid {
  font-size: 15px;
  color: grey;
}
.tickettitle {
  font-size: 15px;
  display: flex;
  justify-content: flex-start;
  margin-top: 20px;
  margin-bottom: 20px;
}
.tickettag {
  font-size: 15px;
  margin-left: 7px;
  border: solid gray 1px;
  border-radius: 3px;
  padding: 7px;
}
.ticketstatus {
  border: solid grey 1px;
  border-radius: 3px;
  padding: 7px;
}
