.kanban-board {
  display: flex;
  gap: 20px;
  justify-content: center;
  padding: 20px;
  background-color: #f4f6fa;
}

.group {
  flex: 1;
}

.ticket-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

.columnHeading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.left {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.left > p {
  padding: 0%;
  margin: 0%;
  font-size: 15px;
  margin-left: 10px;
}

.left p,
.left div {
  margin-right: 10px;
}

.right {
  margin-right: 10px;
  display: flex;
  align-items: center;
}
.left {
  margin-left: 10px;
}

.right img {
  margin-left: 10px;
}
