.App {
  text-align: center;
  width: 100%;
}

h1 {
  margin-bottom: 20px;
}

.header {
  width: 100%;
  height: 80px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.display_button {
  background-color: white;
  border-radius: 5px;
  width: 150px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-left: 40px;
  cursor: pointer;
  border: 1px solid grey;
}
